<template>
	<view>
		<view class="tui-top tui-goods-info">
			<tui-list-cell :hover="false" :lineLeft="false">
				<view class="tui-goods-title">
					我的足迹
				</view>
			</tui-list-cell>
			<block v-for="(item,index) in historyList" :key="index">
				<tui-list-cell :hover="false" padding="0" @tap="detail(item.gid)">
					<view class="tui-goods-item">
						<image :src="item.good.icon" class="tui-goods-img"></image>
						<view class="tui-goods-center">
							<view class="tui-goods-name" style="margin-left: 20rpx;">{{item.good.name}}</view>
							<!-- <view class="tui-goods-attr">{{item.type.name}}</view> -->
						</view>
						<view class="tui-price-left" style="color: #c5201a; font-size: large; margin:auto">
							<view>￥{{fixPrice(item.good.fee)}}</view>
							<!-- <view>x{{item.num}}</view> -->
						</view>
					</view>
				</tui-list-cell>
			</block>
		</view>

		<!--加载loadding-->
		<tui-loadmore v-if="loadding" :index="3" type="red"></tui-loadmore>
		<tui-nomore v-if="nomore" backgroundColor="#f7f7f7"></tui-nomore>
		<!--加载loadding-->

	</view>
</template>

<script>
	let app = getApp()
	export default {
		data() {
			return {
				nomore: false,
				loadding: false,
				
				
				historyList:[],
				next_page_url:null,
				
			};
		},
		
		onLoad() {
			
			this.initialData()
			
		},
		
		computed: {
			fixPhone() {
				return (phone) => {
					return phone.toString().substr(0, 3) + '****' + phone.toString().substr(7)
				}
			},
			fixPrice() {
				return (price) => {
					return price.toFixed(2).toString().split(".")[0] +"."+ price.toFixed(2).toString().split(".")[1]
				}
			},
			firstPrice(){
				return (price) => {
					return price.toFixed(2).toString().split(".")[0]
				}
			},
			lastPrice(){
				return (price) => {
					return price.toFixed(2).toString().split(".")[1]
				}
			}
		
		},
		methods:{
			initialData(){
				this.historyList = uni.getStorageSync('myHistory').data
				this.next_page_url = uni.getStorageSync('myHistory').next_page_url
			},
			
			getHistoryInfo(url) {
				let opts = {
					url: url,
					method: 'GET',
				}
				app.httpRequest(opts).then(res => {
					let rowdata = JSON.parse(res.data).data
					console.log('history:', rowdata)
					this.next_page_url = rowdata.next_page_url //分页器
					if (!this.historyList) {
						let historyList = new Array()
					}
					for (let item of rowdata.data) {
						this.historyList.push(item)
					}
					uni.setStorageSync('myHistory', this.historyList)
				})
			},
			detail(gid){
				uni.navigateTo({
					url:'../productDetail/productDetail?gid='+gid
				})
			}
			
		},
		onReachBottom: function() {
			if (this.next_page_url) {
				this.getHistoryInfo(this.next_page_url)
				this.loadding = true;
			} else {
				this.nomore = true
				this.loadding = false;
			}
		}
		
	}
</script>

